import React, { useContext } from "react";
import "./Checkout.css";
import Grid from "@material-ui/core/Grid";
import CheckoutItems from "./CheckoutItems";
import { CartContext } from "../CartContext";

function Checkout(props) {
  const { item, size, increment } = useContext(CartContext);

  const cartValue = function () {
    let price = 0;
    for (let i = 0; i < item.length; i++) {
      price += parseInt(item[i].price);
    }
    return price.toFixed(2);
  };

  return (
    <div className="checkout__body">
      <Grid container>
        <Grid item={5}>
          <div className="checkout__container">
            <div
              style={{
                fontSize: "30px",
                fontWeight: "500",
                padding: "20px 0px 0px 20px",
              }}>
              Shopping Cart
            </div>
            <div>
              {" "}
              {item.length > 0 ? (
                item.map((value) => <CheckoutItems definition={value} />)
              ) : (
                <div style={{ height: "100vh", margin: "30px" }}>
                  {" "}
                  Please buy something
                </div>
              )}
            </div>
          </div>
        </Grid>
        <Grid item={5}>
          <div
            style={{
              width: "270px",
              height: "100%",
              padding: "20px",
              marginTop: "25px",
              backgroundColor: "white",
            }}>
            <div style={{ fontSize: "26px" }}>
              Subtotal({size} items): ${cartValue()}{" "}
            </div>
            <div style={{ paddingTop: "25px " }}>
              <button className="placeorder__button">Proceed to Buy</button>
            </div>
            <div style={{ paddingTop: "25px " }}>
              <button className="placeorder__button">Empty Cart</button>
            </div>
          </div>
        </Grid>
      </Grid>
    </div>
  );
}

export default Checkout;
